.container__contactUs {
  display: grid;
  grid-template-areas:
    "header header "
    " main right";
  grid-template-columns: 1fr 1fr;
  grid-auto-rows: auto 6fr;
  grid-gap: 1px;
  background-color: #cacaca;
  padding: 10px;
  grid-template-rows: minmax(100px, auto);
}

.container__contactUs > div {
  background-color: rgba(255, 255, 255, 0.8);
  text-align: center;
  padding: 10px;
  font-size: 18px;
}

.cont1 {
  grid-area: header;
}

.cont2 {
  grid-area: main;
}

.cont3 {
  grid-area: right;
}

/* cont1: first div */

.cont1__header {
  text-shadow: 0 0 3px #ff0000;
}

/* cont2: second div */
.cont2__form__textarea {
  resize: none;
  padding: 2%;
  width: 40%;
  border: 2px solid green;
}

/* form inputs */

.cont2__form__inputs {
  width: 40%;
  padding: 12px 20px;
  margin: 8px 0;
  box-sizing: border-box;
  border: 2px solid green;
  border-radius: 4px;
}

/* form button */

.cont2__form__button {
  background-color: #4caf50; /* Green */
  border: none;
  color: white;
  padding: 16px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  transition-duration: 0.4s;
  cursor: pointer;
}

.cont2__form__button {
  background-color: white;
  color: black;
  border: 2px solid #4caf50;
}

.cont2__form__button:hover {
  background-color: #4caf50;
  color: white;
}

